import React,{useEffect,useState} from 'react';
import { GoogleApiWrapper, Map, Marker } from 'google-maps-react';
import './App.css';

const Maps = (props)=> {
  //テキスト内の場所名
  const [place, setPlace] = useState("");
  //マーカーの場所名
  const [name,setName] = useState("");
  //センターの位置
  const [center,setCenter] = useState();
  //初期の位置
  const [currentPosition,setCurrentPosition]=useState();
  //マーカーの位置
  const [marker,setMarker]=useState();

  //検索窓の内容変更の関数
  const handleChangePlace=(e)=> {
    setPlace(e.target.value);
    setName(e.target.value);
  }

  // 検索ボタンを押した時の関数
  const handleSearch=()=> {
    if (place !== "") {
      const geocoder = new props.google.maps.Geocoder();
      // Geocodingの呼び出し
      geocoder.geocode(
        {
          address: place,
          region: "jp",
        },
        function (results, status) {
          if (status === props.google.maps.GeocoderStatus.OK) {
            setCenter(results[0].geometry.location);
            setMarker(results[0].geometry.location);
          }
          else {
            alert(place+"：該当する場所が見つかりませんでした。");
          }
        }
      );
    } 
  };

  //現在地の設定
  //現在地の所得に成功したとき
  const success=(data)=>{
    let currentPosition={lat:data.coords.latitude,lng:data.coords.longitude};
    setCurrentPosition(currentPosition);
    setCenter(currentPosition);
  };
  //現在地の所得に失敗したとき
  const error=()=>{
    alert("現在地の所得に失敗しました。")
  };
  useEffect(()=>{
    navigator.geolocation.getCurrentPosition(success,error);
  },[])

  //現在地に戻す
  const current=()=>{
    setName("現在地");
    setCenter(currentPosition);
  };

  //ドラッグ後の中心座標の所得
  const moveCenter = (_,map) =>{
    let center_latlng = map.getCenter();
    setCenter({lat:center_latlng.latitude,lng:center_latlng.longitude});
  };


  return (
    <>
    {/*検索窓表示*/}
    <div className='Search'>
    <input type="text" onChange={handleChangePlace}/><br/>
    <button onClick={handleSearch} >検索</button><br/>
    <button onClick={current} >現在地</button>
    </div>

    {/*マップ表示*/}
    <Map
      style={{height:'90%',width:'100%'}}
      google = { props.google }
      zoom = { 15 }
      center = {center}
      initialCenter = {currentPosition}
      onDragend = {moveCenter}
    >
      {/*検索結果のマーカー*/}
      <Marker
        title = { name }
        position = {marker}
      />
      {/*現在地のマーカー*/}
      <Marker
        title={"現在地"}
        position={currentPosition}
        icon={{color:'#00ffff'}}/>

    </Map>
      </>
  );
};

export default GoogleApiWrapper({
  apiKey: ("AIzaSyAv5pyugepdOfAs9kA2fIDrwmn0vEMSzPU")
})(Maps);